import {
	uniIcons,
	uniNavBar
} from '@dcloudio/uni-ui';
export default {
	components: {
		uniIcons,
		uniNavBar
	},
	onLoad(option) {
		
		this.icons = [...this.GlobalJson.typdIcons];
		if (option.genre) {
			this.genre = option.genre;
			this.iconclass = this.icons[0].iconclass;
		}
		if (option.item) {
			this.itemObj = JSON.parse(option.item);
			this.label = this.itemObj.label;
			this.iconclass = this.itemObj.iconclass;
			this.genre=this.itemObj.genre;
			for (let i = 0; i < this.icons.length; i++) {
				if (this.icons[i].iconclass == this.iconclass) {
					this.inds = i;
				}
			}
		}
	},
	data() {
		return {
			label: "",
			iconclass: "",
			itemObj: {},
			inds: 0,
			icons: [],
			genre: 1,
			Token: uni.getStorageSync('userId') || '',
		}
	},
	methods: {
		// 返回上一页面
		toback() {
			uni.navigateBack({
				delta: 1
			});
		},
		// 选中某个图标
		handleIocn(iconclass, index) {
			this.inds = index;
			this.iconclass = iconclass;
		},
		// 保存数据
		save() {
			if (!this.label) {
				uni.showToast({
					title: '请输入类别名称',
					icon: 'none'
				})
				return
			}
			uni.showLoading({
				title:'数据保存中...',
				mask:true
			})
			const _data = {
				pay: this.itemObj._id?3:1,
				token: this.Token,
				genre: this.genre,
				label: this.label,
				iconclass: this.iconclass
			},_this=this;
			if(this.itemObj._id){
				_data._id=this.itemObj._id;
			}
			uniCloud.callFunction({
				name: 'settype',
				data: _data
			}).then((res) => {
				console.log('res:', res)
				uni.hideLoading();
				uni.showToast({
					title: '保存成功',
					mask:true
				})
				setTimeout(()=>{
					_this.toback();
				},2000)
			}).catch((err) => {
				uni.hideLoading();
				uni.showModal({
					content: `操作失败，请重新操作`,
					showCancel: false
				})
			})

		}
	}
}
